#login {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100%;
}

input[type=text],input[type=password] {
  width: 100%;
  height: 30px;
  font-size: 14pt;
  border: 1px solid #999999;
  border-radius: 3px;
  padding: 6px;
  margin-bottom: 4px;
}

button {
  width: 100%;
  height: 40px;
  font-size: 14pt;
  background-color: #aaaaaa;
  border: 1px solid #999999;
  border-radius: 3px;
  padding: 6px;
}

.error {
  width: 100%;
  height: 30px;
  background-color: red;
  color: white;
  border-radius: 3px;
  margin-top: 10px;
  padding: 6px;
}



